  text-align: center; }
  .pre-loader .spinner {
    width: 100px;
    height: 50px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    text-align: center; }
    .pre-loader .spinner .ball {
      width: 20px;
      height: 20px;
      background-color: #444E5A;
      border-radius: 50%;
      display: inline-block;
      -webkit-animation: motion 3s cubic-bezier(0.77, 0, 0.175, 1) infinite;
